import React, {useEffect, useState} from "react";
import axios from "axios";
import {Button, List} from "antd";
import {BASE_URL} from "../../utils/request";
import {connect} from 'react-redux';
import {withRouter} from 'react-router-dom';

function Left(props) {
  let {oneTitle, twoList = [], setMainDataMd, setMainDataLoading, rootState, location, history} = props;
  let [activeIndex, setActiveIndex] = useState(-1);
  let [oneTitleState, setOneTitleState] = useState(-1);
  let [twoListState, setTwoListState] = useState([]);

  useEffect(() => {
    let locationSplit = location.pathname.split('/');
    if (!locationSplit[1]) {
      setTwoListState(twoList);
      setOneTitleState(oneTitle);
    } else {
      let filterTwoListElement = rootState.filter(item => item.uid.includes(locationSplit[1]))[0];
      let isRootLength = rootState.length !== 0;
      if (filterTwoListElement && filterTwoListElement.children) {
        isRootLength && setTwoListState(filterTwoListElement.children);
      }
      isRootLength && rootState.forEach(item => {
        if (item.uid.includes(locationSplit[1])) {
          setOneTitleState(item.title);
        }
      });
      isRootLength && twoListState && twoListState.forEach((item, index) => {
        if (item.uid.includes(locationSplit[locationSplit.length - 1])) {
          setActiveIndex(index)
        }
      });
    }
  }, [oneTitle,location.pathname, rootState, twoList, twoListState]);

  return <>
    <List
      itemLayout="horizontal"
      className={'list'}
      dataSource={twoListState}
      renderItem={(item, index) => {
        return <List.Item
          className={index === activeIndex ? 'active' : ''}
          onClick={async () => {
            if (index === activeIndex) {
              return
            }
            setActiveIndex(index);
            const url = `${BASE_URL}/${encodeURIComponent(oneTitleState)}/${encodeURIComponent(item.title)}`;
            setMainDataLoading(true);
            const pathName1 = location.pathname.split('/')[1];
            history.push(`/${pathName1}/${item.uid.split('-')[0]}`);
            try {
              const mainDataMd = await axios.get(url);
              setMainDataMd(mainDataMd.data);
            } catch (e) {

            } finally {
              setMainDataLoading(false);
            }
          }}>
          <List.Item.Meta
            title={<Button
              type='link'
              id={index === activeIndex ? 'activeFont' : 'commonFont'}
            >{item.filterTitle || item.title}</Button>}
          />
        </List.Item>
      }}
    />
  </>

}

const mapStateToProps = state => {
  return {
    rootState: state.rootReducers
  }
};
export default connect(mapStateToProps)(withRouter(Left));
