import React, {useEffect, useRef, useState} from 'react';
import axios from 'axios';
import 'antd/dist/antd.css'
import FirstLevelNavigation from './Interview/FirstLevelNavigation'
import SecondaryNavigation from "./Interview/SecondaryNavigation";
import DisplayArea from "./Interview/DisplayArea";
import {BASE_URL} from "../utils/request";
import {connect} from 'react-redux'
import {withRouter} from 'react-router-dom'
import './Interview/Interview.css';
import './mo.css'
import ValineComponent from "./Interview/ValineComponent";
// 设置高亮的语言
// import { jsx, javascript, sass, scss } from "react-syntax-highlighter/dist/esm/languages/prism";
// Fragment // 16+ template

const Interview = (props) => {
  const {setRootData, location} = props;
  const imgMainArr = [
    'https://cdn.jsdelivr.net/gh/IceRain-mvc/cdn/img/%E5%85%A8%E5%B1%8F%E6%98%BE%E7%A4%BA.svg',
    'https://cdn.jsdelivr.net/gh/IceRain-mvc/cdn/img/%E7%BC%A9%E5%B0%8F.svg'
  ];
  const imgOneArr = [
    'https://cdn.jsdelivr.net/gh/IceRain-mvc/cdn/img/%E9%9A%90%E8%97%8F.svg',
    'https://cdn.jsdelivr.net/gh/IceRain-mvc/cdn/img/%E6%98%BE%E7%A4%BA.svg'
  ];
  const imgTwoArr = [
    'https://cdn.jsdelivr.net/gh/IceRain-mvc/cdn/img/%E6%98%BE%E7%A4%BA.svg',
    'https://cdn.jsdelivr.net/gh/IceRain-mvc/cdn/img/%E9%9A%90%E8%97%8F.svg'
  ];
  const refMainImg = useRef();
  const refOneImg = useRef();
  const refTwoImg = useRef();
  const refOne = useRef();
  const refTwo = useRef();
  const refMain = useRef();
  const refQrCode = useRef();

  const [dataList, setDataList] = useState([]);
  const [twoList, setTwoList] = useState([]);
  const [oneTitle, setOneTitle] = useState('');
  const [mainDataMd, setMainDataMd] = useState('');
  const [mainDataLoading, setMainDataLoading] = useState(false);
  /*控制点击第一列第二列的高亮状态*/
  const [flag, setFlag] = useState(-1);
  const [showClose, setShowClose] = useState(false);
  const [showQr, setShowQr] = useState(true);
  const [zoomMainImg] = useState(imgMainArr[0]);
  const [zoomOneImgUrl] = useState(imgOneArr[0]);
  const handleShowOne = () => {
    if (refOneImg.current.style.transform === 'rotateY(180deg)') {
      refOneImg.current.style.transform = 'rotateY(0deg)';
      // 处理一级显示
      refOne.current.style.width = '15vw';
      refOne.current.style.opacity = '1';
      refOne.current.style.padding = '20px';

    } else {
      refOneImg.current.style.transform = 'rotateY(180deg)';
      // 处理一级隐藏
      refOne.current.style.width = '0vw';
      refOne.current.style.opacity = '0';
      refOne.current.style.padding = '0px';
    }
  };

  function showTwoContent() {
    setTimeout(() => {
      refTwoImg.current.style.opacity = '1';
      refTwoImg.current.style.transform = 'rotateY(180deg)';
    }, 0);
    // 处理一级显示
    // setShowTwo(true);
    refTwo.current.style.width = '25vw';
    refTwo.current.style.opacity = '1';
    refTwo.current.style.padding = '20px  20px 20px 0';
    refTwo.current.style.borderLeft = '1px solid #dddddd';
  }

  const handleShowTwo = () => {
    if (refTwoImg.current.style.transform === 'rotateY(180deg)') {
      refTwoImg.current.style.transform = 'rotateY(0deg)';
      refTwo.current.style.width = '0vw';
      refTwo.current.style.opacity = '0';
      refTwo.current.style.padding = '0px';
    } else {
      showTwoContent();
    }
  };
  const handleZoomMain = () => {
    refMain.current.requestFullscreen();
    refMain.current.style.backgroundColor = '#ffffff';
  };
  const setActiveTwo = (firstIndex) => {
    setFlag(firstIndex);
    if (firstIndex !== -1) {
      showTwoContent();
    }
  };
  useEffect(() => {
    async function fetchData() {
      let res = await axios.get(`${BASE_URL}/data.json`);
      setDataList(res.data);
      setRootData(res.data);
    }

    fetchData();
    if (location.pathname.split('/')[2]) {
      setFlag(0);
      showTwoContent();
    } else {
      refTwoImg.current.style.opacity = '0';
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    setShowQr(true);
  }, [location.pathname]);

  function mouseEnterQrCode(e) {
    setShowClose(true)

  }

  function mouseLeaveQrCode(e) {
    setShowClose(false)
  }

  return (
    <div className={'container_Box'}>
      <div className={'layerIcon'}>
        <img
          ref={refOneImg}
          onClick={handleShowOne}
          className={'one_hide_show'} src={zoomOneImgUrl} alt=""/>
        <img className={'two_hide_show'}
             ref={refTwoImg}
             src={imgTwoArr[0]}
             onClick={handleShowTwo}
             alt=""/>
        <img className={'zoom'}
             ref={refMainImg}
             onClick={handleZoomMain}
             src={zoomMainImg} alt=""/>
      </div>
      <div className={'one'} ref={refOne}>
        <FirstLevelNavigation
          dataList={dataList}
          setActiveTwo={setActiveTwo}
          setOneTitle={setOneTitle}
          setTwoList={setTwoList}/>
      </div>
      <div className={'two'} ref={refTwo}>
        <SecondaryNavigation
          oneTitle={oneTitle}
          twoList={twoList}
          flag={flag}
          setMainDataLoading={setMainDataLoading}
          setMainDataMd={setMainDataMd}/>
      </div>
      <div className={'main'} ref={refMain}>
        {showQr && <div onMouseLeave={mouseLeaveQrCode} onMouseEnter={mouseEnterQrCode} className={'ReceivingCode'}
                        ref={refQrCode}>
          {showClose && <img onClick={() => {
            setShowQr(false);
            setShowClose(false);
          }} className={'closeQr'} src="https://cdn.jsdelivr.net/gh/IceRain-mvc/cdn/img/close.png" alt="关闭"/>}
          <img src="https://cdn.jsdelivr.net/gh/IceRain-mvc/cdn/img/7051633947053_.pic_hd.jpg" alt=""/>
        </div>}
        <DisplayArea mainDataMd={mainDataMd} mainDataLoading={mainDataLoading}/>
        <ValineComponent/>
      </div>
    </div>
  );
};
const mapStateToProps = (state) => {
  return {}
};
const mapDispatchToDispatch = (dispatch) => {
  return {
    setRootData(data) {
      dispatch({type: 'PUSH_ALL', payload: data});
    }
  }
};

export default connect(mapStateToProps, mapDispatchToDispatch)(withRouter(Interview));
